<template>
	<section id="login" class="container-fluid">
		<loading :active.sync="isLoading" 
        :can-cancel="true"
        :is-full-page="fullPage"></loading>
			
		<div class="row justify-content-center">
			<div class="col-6 imgx">
				<img src="/img/login.png" alt="">
			</div>
			<div class="col-6   another  align-items-center justify-content-center">
			<h1 class="header text-center login-class">LOGIN</h1>
				<div class="container-fluid">
       <div class=" frm">
    <form class="login-form">
      <input   class="input" type="text" name="username" v-model="username" placeholder="Enter Username/email/telephone" required/>
			<input  class="input" type="password" name="password" v-model="password" placeholder="Enter Password" required/>
      <div>
        <span class="error">{{ loginError }}</span>
      </div>
		  <router-link to="/Forgotpassword"><p class="forgot">forgot password?</p></router-link>
      <button @click.prevent="authenticate()">LOGIN</button>
			<p class="message">Not registered?<router-link to="/register"><a>Create an account</a></router-link></p>
    </form>
  </div>
     </div>
			</div>
		</div>
	</section>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import Swal from 'sweetalert2/dist/sweetalert2.js';
import 'sweetalert2/src/sweetalert2.scss';



export default {
	data(){
    return {  
      username:'',
			password:'',
			isLoading: false,
      fullPage: true
    }
	},
	 components: {
            Loading,
  },
	methods: {

    	async authenticate(){
			this.isLoading = true;
      let res = await this.login({username: this.username,password: this.password});
                // simulate AJAX
                setTimeout(() => {
                  this.isLoading = false
                },5000)
      
    },

		...mapActions(['login']),
    
  },
	computed: {
    ...mapState(['loginError']),
	}
}
</script>

